.home-page {
  width: 100vw;
  height: 100vh;
  background-color: #1c6dd0;
  text-transform: uppercase;
}
.home-page .cursor {
  display: none;
  pointer-events: none;
}
@media screen and (min-width: 900px) {
  .home-page .cursor {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    z-index: 100;
    background-color: #1c6dd0;
  }
}
.home-page .cursor.foto {
  background-color: #fdf6f0;
}
.home-page .nav-home-page {
  font-size: 1vw;
  width: 100vw;
  position: absolute;
  color: #fdf6f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.home-page .nav-home-page .nav-kiri {
  padding: 3rem 0 3rem 3rem;
  margin-right: 1rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.home-page .nav-home-page .nav-kanan {
  padding: 3rem 3rem 3rem 0;
  margin-left: 1rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #1c6dd0;
}
.home-page .home-page-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 900px) {
  .home-page .home-page-container {
    flex-direction: row;
  }
}
.home-page .home-page-container .fotografer-section {
  font-size: 1.5vw;
  color: #fdf6f0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}
.home-page .home-page-container .fullstack-section {
  font-size: 1.5vw;
  color: #1c6dd0;
  background-color: #fdf6f0;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.home-page-transition {
  z-index: -1;
  top: 0;
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: #1c6dd0;
  text-transform: uppercase;
}
.home-page-transition .cursor {
  display: none;
  pointer-events: none;
}
@media screen and (min-width: 900px) {
  .home-page-transition .cursor {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    z-index: 100;
    background-color: #1c6dd0;
  }
}
.home-page-transition .cursor.foto {
  background-color: #fdf6f0;
}
.home-page-transition .nav-home-page {
  overflow: hidden;
  font-size: 1vw;
  width: 100vw;
  position: absolute;
  color: #fdf6f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.home-page-transition .nav-home-page .nav-kiri {
  padding: 3rem 0 3rem 3rem;
  margin-right: 1rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.home-page-transition .nav-home-page .nav-kanan {
  padding: 3rem 3rem 3rem 0;
  margin-left: 1rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #1c6dd0;
}
.home-page-transition .home-page-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
}
.home-page-transition .home-page-container h1 {
  overflow: hidden;
}
.home-page-transition .home-page-container h2 {
  overflow: hidden;
}
@media screen and (min-width: 900px) {
  .home-page-transition .home-page-container {
    flex-direction: row;
  }
}
.home-page-transition .home-page-container .fotografer-section {
  white-space: nowrap;
  font-size: 1.5vw;
  color: #fdf6f0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}
.home-page-transition .home-page-container .fullstack-section {
  font-size: 1.5vw;
  color: #1c6dd0;
  background-color: #fdf6f0;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}